import React, { useState, useEffect } from 'react';
import { Box, Typography, Paper, Modal } from '@mui/material';
import WifiIcon from '@mui/icons-material/Wifi';
import ReceiptIcon from '@mui/icons-material/Receipt';
import PersonIcon from '@mui/icons-material/Person';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners';

function AcessoRapido() {
  const navigate = useNavigate();

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loadingMessage, setLoadingMessage] = useState('');
  const [primaryColor, setPrimaryColor] = useState('#28a745'); // Cor padro para o CircleLoader
  const [iconColor, setIconColor] = useState('#5b5b5b'); // Cor padro dos cones
  const [modules, setModules] = useState({}); // Estado para armazenar os mdulos disponveis

  // Define the table ID for modules
  const modulesTableId = 'msafdyz6sew21f1'; // Tabela de mdulos permitidos
  const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Token de autenticao
  const baseUrl = 'https://nocodb.nexusnerds.com.br/api/v2/tables/'; // URL base

// Funo para buscar os mdulos disponveis
const fetchModules = async () => {
  try {
    const response = await fetch(`${baseUrl}${modulesTableId}/records`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'xc-token': token,
      },
    });

    if (response.ok) {
      const data = await response.json();
      console.log('Dados dos mdulos recebidos:', data); // Log para verificar os dados recebidos

      if (data.list.length > 0) {
        const availableModules = data.list[0]; // Supondo que voc s tenha uma linha com os checkboxes
        console.log('Mdulos atualizados:', availableModules); // Verificar os mdulos antes de setar
        setModules(availableModules); // Atualiza o estado com os mdulos
      }
    } else {
      console.error('Erro ao buscar os mdulos:', response.statusText);
    }
  } catch (error) {
    console.error('Erro ao buscar os mdulos:', error);
  }
};

useEffect(() => {
  // Funo que chama os mdulos e as configuraes
  const fetchData = () => {
    fetchModules();     // Busca os mdulos disponveis
    fetchAppSettings(); // Busca as configuraes do app
  };

  // Chama a funo imediatamente quando o componente  montado
  fetchData();

  // Configura o intervalo para chamar a funo a cada 5 segundos
  const intervalId = setInterval(() => {
    fetchData();
  }, 5000); // 5000 ms = 5 segundos

  // Limpa o intervalo quando o componente for desmontado
  return () => clearInterval(intervalId);
}, []);


  // Funo para buscar as configuraes do app
  const fetchAppSettings = async () => {
    const colorsTableId = 'mn37trxp7ai1efw'; // Tabela de cores

    try {
      const response = await fetch(`${baseUrl}${colorsTableId}/records`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'xc-token': token,
        },
      });

      if (response.ok) {
        const data = await response.json();
        if (data.list.length > 0) {
          const settings = data.list[0];

          // Atualiza a cor primria (CircleLoader) com o valor recebido do NocoDB
          if (settings.primaryColor) {
            setPrimaryColor(settings.primaryColor);
          }

          // Atualiza a cor dos cones com o valor recebido do NocoDB
          if (settings.iconColor) {
            setIconColor(settings.iconColor);
          }
        }
      } else {
        console.error('Erro ao buscar as configuraes do app:', response.statusText);
      }
    } catch (error) {
      console.error('Erro ao buscar as configuraes do app:', error);
    }
  };

  // Funo para lidar com o clique em Minhas Faturas
  const handleMinhasFaturasClick = async () => {
    // Aqui voc pode adicionar a lgica para lidar com o clique
    navigate('/financeiro');
  };

  useEffect(() => {
    fetchModules(); // Busca os mdulos disponveis
    fetchAppSettings(); // Busca as configuraes do app
  }, []);

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h6" sx={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '20px', color: '#5b5b5b' }}>
        Acesso rpido
      </Typography>
      <Box className="acesso-rapido-scroll" sx={{ display: 'flex', overflowX: 'auto', gap: '15px' }}>
        {modules.SubModulo_MWiFi && (
          <Paper
            elevation={0}
            sx={{
              minWidth: '120px',
              textAlign: 'center',
              borderRadius: '10px',
              backgroundColor: '#f5f5f5',
              margin: '0',
              cursor: 'pointer'
            }}
            onClick={() => navigate('/wifi')}
          >
            <WifiIcon sx={{ fontSize: 30, color: iconColor, padding: '20px' }} />
            <Typography sx={{ marginTop: '10px', fontSize: '14px', color: iconColor }}>Meu Wi-Fi</Typography>
          </Paper>
        )}
        
        {modules.SubModulo_MFaturas && (
          <Paper
            elevation={0}
            sx={{
              minWidth: '120px',
              textAlign: 'center',
              borderRadius: '10px',
              backgroundColor: '#f5f5f5',
              margin: '0',
              cursor: 'pointer'
            }}
            onClick={handleMinhasFaturasClick}
          >
            <ReceiptIcon sx={{ fontSize: 30, color: iconColor, padding: '20px' }} />
            <Typography sx={{ marginTop: '10px', fontSize: '14px', color: iconColor }}>Minhas Faturas</Typography>
          </Paper>
        )}

        {modules.SubModulo_MAtendimentos && (
          <Paper
            elevation={0}
            sx={{
              minWidth: '120px',
              textAlign: 'center',
              borderRadius: '10px',
              backgroundColor: '#f5f5f5',
              margin: '0',
              cursor: 'pointer'
            }}
            onClick={() => navigate('/detalhes')}
          >
            <PersonIcon sx={{ fontSize: 30, color: iconColor, padding: '20px' }} />
            <Typography sx={{ marginTop: '10px', fontSize: '14px', color: iconColor }}>Atendimentos</Typography>
          </Paper>
        )}
      </Box>

      <Modal
        open={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        aria-labelledby="loading-modal-title"
      >
        <Box
          sx={{
            position: 'absolute',
            bottom: '20px',
            left: '50%',
            transform: 'translateX(-50%)',
            width: '90%',
            maxWidth: '300px',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 2,
            borderRadius: '12px',
            textAlign: 'center',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          <CircleLoader className="loading-modal" color={primaryColor} size={25} />
          <Typography id="loading-modal-title" variant="h6" sx={{ marginTop: 2 }}>
            {loadingMessage}
          </Typography>
        </Box>
      </Modal>
    </Box>
  );
}

export default AcessoRapido;
